import React,{Component} from 'react';
import classes from './Auth.module.css';
import Spinner from '../../components/UI/Spinner/Spinner';
import Input from '../../components/UI/Input/Input';
import Button from '../../components/UI/Button/Button';
import {connect} from 'react-redux';
import * as actionCreators from '../../store/actions/index';
import {Redirect} from 'react-router-dom';

class Auth extends Component{


componentDidMount(){

    if(!this.props.buildingBurger && this.props.authRedirectPath!=='/'){

        this.props.onSetAuthRedirectPath()
    }
}

componentWillMount(){

    console.log("will unmount");
  }
    
    checkValidity(value,rules){
        let isValid=true;

        if(rules.required){
            isValid=value.trim() !=='' && isValid ;
        }
        
        if(rules.minLength){
            isValid=value.trim().length >= rules.minLength  && isValid;
        }
        

        if(rules.maxLength){
            isValid=value.trim().length <= rules.maxLength  && isValid;
        }
       

        return isValid;
    }


    inputChangeHandler=(event,controlName)=>{

        const updatedControls={

            ...this.state.controls,
            [controlName]:{

                ...this.state.controls[controlName],
                value:event.target.value,
                valid:this.checkValidity(event.target.value,this.state.controls[controlName].validation),
                touched:true
            }
        }

        this.setState({controls:updatedControls})
    }


    state={


        controls:{


            email:{
                elementType:"input",
                elementConfig:{
                    type:'email',
                    placeholder:'Email Address'
                },
                value:'',
                validation:{
                    required:true,
                    isEmail:true
                },
                valid:false,
                touched:false
            },
            password:{
                elementType:"input",
                elementConfig:{
                    type:'password',
                    placeholder:'Password'
                },
                value:'',
                validation:{
                    required:true,
                    minLength:7
                },
                valid:false,
                touched:false
            }
        },
        isSignup:true


    }

    submitHandler=(event)=>{
        event.preventDefault()
        this.props.onAuth(this.state.controls.email.value,this.state.controls.password.value,this.state.isSignup)
    }

    switchAuthModeHandler=()=>{

        this.setState(prevState=>{
            
            return{
                isSignup:!(prevState.isSignup)
            }
        })
    }

    



    render(){



        const formElementsArray=[];
        for(let key in this.state.controls){

            formElementsArray.push({
                id:key,
                config:this.state.controls[key]
            })
        }

        let form=formElementsArray.map(formElement=>{

            return( <Input
            key={formElement.id} elementType={formElement.config.elementType}
            elementConfig={formElement.config.elementConfig}
            value={formElement.config.value}
            changed={(event)=>this.inputChangeHandler(event,formElement.id)}
            invalid={!formElement.config.valid}
            shouldValidate={formElement.config.validation}
            touched={formElement.config.touched}/>)
        })

        if(this.props.loading){
            form=<Spinner></Spinner>
        }

        let errorMessage=null;
        
        if(this.props.error){
            errorMessage=<p>{this.props.error.message}</p>
        }

        let redirect=null

        if(this.props.isAuth){
            console.log("here");
            redirect=<Redirect to ={this.props.authRedirectPath}/>
        }


        return(

            <div className={classes.Auth}>
                {redirect}
                {errorMessage}
                <form onSubmit={this.submitHandler}>
                     {form}
                    <Button btnType="Success">Submit</Button>
                </form>
        <           Button btnType="Danger" clicked={this.switchAuthModeHandler}>Swith to {this.state.isSignup?'SIGN-IN':'SIGN-UP'}</Button>
        
            </div>
        )
    }
}


const mapStateToProps=(state)=>{

    return {
        loading:state.auth.loading,
        error:state.auth.error,
        isAuth:state.auth.token!==null,
        token:state.auth.token,
        buildingBurger:state.burgerBuilder.building,
        authRedirectPath:state.auth.authRedirectPath
        
    }
}


const mapDispatchToProps=(dispatch)=>{

    return{
        onAuth:(email,password,isSignup)=>dispatch(actionCreators.auth(email,password,isSignup)),
        onSetAuthRedirectPath:()=>dispatch(actionCreators.setAuthRedirectPath("/"))
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Auth);


